.Container {
    text-align: center;
    display: grid;
    grid-template-columns: 15% 85%;
    grid-template-rows: 15% 85%;
    padding-top: 20px;
    grid-gap: none;
}

.HitPoints{
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    background-color: rgba(250, 128, 114, 0.275);
    border-radius: 16px;
    padding: 6px;
    grid-row: 1/2;
    grid-column: 2/3;
    width: 60%;
    margin: 0% 0px 0px 15%;
}

.Heart {
    height: 50px;
    width: 50px;
    justify-self: center;
}

.Pokemon {
    height: 60%;
    width: 60%;
    grid-row: 2/3;
    grid-column: 2/3;
    margin: 5% 0px 0px 15%;
}

.Ash {
    width: 100%;
    height: 65%;
    grid-row: 2/3;
    grid-column: 1/2;
}